import { useState } from "react";
import { useMutation } from "@apollo/client";
import { ADD_EMPLOYEE } from "../mutations/employeeMutation";
import { GET_EMPLOYEES } from "./EmployeeDirectory";

const EmployeeCreate = () => {
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [age, setAge] = useState("");
  const [title, setTitle] = useState("");
  const [department, setDepartment] = useState("");
  const [employeeType, setEmployeeType] = useState("");
  const [currentStatus, setCurrentStatus] = useState(1); // Default to 1 (working)

  const [addEmployee] = useMutation(ADD_EMPLOYEE, {
    variables: {
      firstName,
      lastName,
      age: parseInt(age),
      title,
      department,
      employeeType,
      currentStatus: parseInt(currentStatus),
    },
    refetchQueries: [{ query: GET_EMPLOYEES }],
  });

  const onSubmit = (e) => {
    e.preventDefault();
    addEmployee();

    // Reset form fields after submission
    setFirstName("");
    setLastName("");
    setAge("");
    setTitle("");
    setDepartment("");
    setEmployeeType("");
    setCurrentStatus(1); // Reset to default value
  };

  return (
    <div>
      <h2>Add Employee</h2>
      <form onSubmit={onSubmit} style={{ marginBottom: "20px" }}>
        <div style={{ marginBottom: "10px" }}>
          <label>First Name</label>
          <input
            type="text"
            value={firstName}
            onChange={(e) => setFirstName(e.target.value)}
            style={{ display: "block", width: "100%", padding: "8px", marginTop: "5px" }}
          />
        </div>
        <div style={{ marginBottom: "10px" }}>
          <label>Last Name</label>
          <input
            type="text"
            value={lastName}
            onChange={(e) => setLastName(e.target.value)}
            style={{ display: "block", width: "100%", padding: "8px", marginTop: "5px" }}
          />
        </div>
        <div style={{ marginBottom: "10px" }}>
          <label>Age</label>
          <input
            type="number"
            value={age}
            onChange={(e) => setAge(e.target.value)}
            style={{ display: "block", width: "100%", padding: "8px", marginTop: "5px" }}
          />
        </div>
        <div style={{ marginBottom: "10px" }}>
          <label>Title</label>
          <input
            type="text"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            style={{ display: "block", width: "100%", padding: "8px", marginTop: "5px" }}
          />
        </div>
        <div style={{ marginBottom: "10px" }}>
          <label>Department</label>
          <input
            type="text"
            value={department}
            onChange={(e) => setDepartment(e.target.value)}
            style={{ display: "block", width: "100%", padding: "8px", marginTop: "5px" }}
          />
        </div>
        <div style={{ marginBottom: "10px" }}>
          <label>Employee Type</label>
          <input
            type="text"
            value={employeeType}
            onChange={(e) => setEmployeeType(e.target.value)}
            style={{ display: "block", width: "100%", padding: "8px", marginTop: "5px" }}
          />
        </div>
        <div style={{ marginBottom: "10px" }}>
          <label>Current Status</label>
          <input
            type="number"
            value={currentStatus}
            onChange={(e) => setCurrentStatus(e.target.value)}
            style={{ display: "block", width: "100%", padding: "8px", marginTop: "5px" }}
          />
        </div>
        <button type="submit" style={{ padding: "10px 15px" }}>Submit</button>
      </form>
      
      <div>
        <h2>Employee Table</h2>
      </div>
    </div>
  );
};

export default EmployeeCreate;
