import { gql } from "@apollo/client";

export const ADD_EMPLOYEE = gql`
  mutation addEmployee(
    $firstName: String!
    $lastName: String!
    $age: Int!
    $title: String!
    $department: String!
    $employeeType: String!
    $currentStatus: Int!
  ) {
    addEmployee(
      firstName: $firstName
      lastName: $lastName
      age: $age
      title: $title
      department: $department
      employeeType: $employeeType
      currentStatus: $currentStatus
    ) {
      id
      firstName
      lastName
      age
      title
      department
      employeeType
      currentStatus
    }
  }
`;
